import React, { Component } from 'react';
import './iopage.scss'
import * as FiliterReg from '../Function/FiliterReg'
import moment from 'moment';
import * as WardState from '../Function/Wardstate'
import * as ApiProvider from '../API/ApiProvider'
import X2JS from 'x2js';

class IOpage extends Component {
    state = {
        QDC: [],
        PreStatus: null,
        PreIntervalStatus: null,
        PreCID: null,
        PrePickerDate: null
    }
    getAPI(caseid, pickerdate, intervalStatus, status) {
        if (caseid === undefined || caseid === null) {
            return null
        }
        if (pickerdate === undefined || pickerdate === null) {
            return null
        }
        if (intervalStatus === undefined || intervalStatus === null) {
            return null
        }
        if (status === undefined || status === null) {
            return null
        }
        function formatTimer(pickerdate, interstatus, status) {
            const endTimeStamp = pickerdate + 31 * 60 * 60 * 1000
            if (status === "Crossection") {
                return {
                    start: moment(endTimeStamp - (3 * 24 * 60 * 60 * 1000)).format("YYYYMMDDHHmm"),
                    end: moment(endTimeStamp).format("YYYYMMDDHHmm")
                }
            }
            if (interstatus === 0) {
                return {
                    start: moment(endTimeStamp - (24 * 60 * 60 * 1000)).format("YYYYMMDDHHmm"),
                    end: moment(endTimeStamp).format("YYYYMMDDHHmm")
                }
            }
            if (interstatus === 1) {
                return {
                    start: moment(endTimeStamp - (3 * 24 * 60 * 60 * 1000)).format("YYYYMMDDHHmm"),
                    end: moment(endTimeStamp).format("YYYYMMDDHHmm")
                }
            }
            if (interstatus === 2) {
                return {
                    start: moment(endTimeStamp - (7 * 24 * 60 * 60 * 1000)).format("YYYYMMDDHHmm"),
                    end: moment(endTimeStamp).format("YYYYMMDDHHmm")
                }
            }
            return {
                start: moment(endTimeStamp - (14 * 24 * 60 * 60 * 1000)).format("YYYYMMDDHHmm"),
                end: moment(endTimeStamp).format("YYYYMMDDHHmm")
            }
        }
        const xm2 = new X2JS()
        const timer = formatTimer(pickerdate, intervalStatus, status)
        ApiProvider.getQDC(caseid, timer.start, timer.end)
            .then(data => {
                const Result = xm2.xml2js(data.data).outmsg
                if (Number(Result.ret) === 0) {
                    const parsedata = Result.resconts.rescont
                    if (Array.isArray(parsedata)) {
                        this.setState({ QDC: parsedata })
                    }
                    else {
                        this.setState({ QDC: [parsedata] })
                    }
                }

            })
            .catch(err => {
                console.log("getQDC NG " + JSON.stringify(err));
            });
    }
    componentDidMount() {
        const { Status, IntervalStatus, caseinfo, PickerDate } = this.props
        this.setState({ PreStatus: Status, PreIntervalStatus: IntervalStatus, PreCID: caseinfo.CASEID, PrePickerDate: PickerDate })
        this.getAPI(caseinfo.CASEID, PickerDate, IntervalStatus, Status)
    }
    componentDidUpdate() {
        const { Status, IntervalStatus, caseinfo, PickerDate } = this.props
        const prestatus = this.state.PreStatus
        const preinterstatus = this.state.PreIntervalStatus
        const precid = this.state.PreCID
        const prepickerdate = this.state.PrePickerDate
        if (prepickerdate !== PickerDate || caseinfo.CASEID !== precid || IntervalStatus !== preinterstatus || Status !== prestatus) {
            console.log(Status, IntervalStatus, caseinfo, PickerDate)
            this.setState({ PreStatus: Status, PreIntervalStatus: IntervalStatus, PreCID: caseinfo.CASEID, PrePickerDate: PickerDate })
            this.getAPI(caseinfo.CASEID, PickerDate, IntervalStatus, Status)
        }
    }
    cellRender(arr) {
        const { Status, IntervalStatus } = this.props
        const mapcell = arr.map(function (value, index, array) {
            return (
                <div key={index} style={{ border: "1px solid #cccccc", borderCollapse: "collapse", display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <t2>{parseInt(value)}</t2>
                </div>
            );
        })
        if (Status === "Crossection") {
            return (
                <div style={{ display: "grid", gridTemplateColumns: "repeat(4,1fr)" }}>
                    {mapcell}
                </div>
            );
        }
        else {
            if (IntervalStatus === 0) {
                return (
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(24,1fr)" }}>
                        {mapcell}
                    </div>
                );
            }
            if (IntervalStatus === 1) {
                return (
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(18,1fr)" }}>
                        {mapcell}
                    </div>
                );
            }
            if (IntervalStatus === 2) {
                return (
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(21,1fr)" }}>
                        {mapcell}
                    </div>
                );
            }
            if (IntervalStatus === 3) {
                return (
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(14,1fr)" }}>
                        {mapcell}
                    </div>
                );
            }
        }
    }
    render() {
        function pickerTimeArray(pickerdate, intervalstatus, status) {
            const et = pickerdate + (31 * 60 * 60 * 1000)
            var timearray = []
            if (status === "Crossection") {
                const st = pickerdate + (7 * 60 * 60 * 1000)
                for (let index = 0; index < 3; index++) {
                    const time = st + (index * 8 * 60 * 60 * 1000)
                    timearray.push(time)
                }
            }
            else {
                //IntervalStatus 0:1å¤© 1:3å¤© 2:ä¸é± 3:å©é±
                if (intervalstatus === 0) {
                    const st = et - (24 * 60 * 60 * 1000)
                    for (let index = 0; index < 24; index++) {
                        const time = st + (index * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
                if (intervalstatus === 1) {
                    const st = et - (3 * 24 * 60 * 60 * 1000)
                    for (let index = 0; index < 18; index++) {
                        const time = st + (index * 4 * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
                if (intervalstatus === 2) {
                    const st = et - (7 * 24 * 60 * 60 * 1000)
                    for (let index = 0; index < 21; index++) {
                        const time = st + (index * 8 * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
                if (intervalstatus === 3) {
                    const st = et - (14 * 24 * 60 * 60 * 1000)
                    for (let index = 0; index < 14; index++) {
                        const time = st + (index * 24 * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
            }
            return timearray
        }

        function starttime(status, intervalstatus, time) {
            if (status === "Crossection") {
                return time - (24 * 60 * 60 * 1000)
            }
            if (intervalstatus === 0) {
                return time - (24 * 60 * 60 * 1000)
            }
            if (intervalstatus === 1) {
                return time - (3 * 24 * 60 * 60 * 1000)
            }
            if (intervalstatus === 2) {
                return time - (7 * 24 * 60 * 60 * 1000)
            }
            return time - (14 * 24 * 60 * 60 * 1000)

        }

        function IntervalToDataArray(startTime, status, intervalstatus, element) {
            if (Array.isArray(element)) {
                if (status === 'Crossection') {
                    const filterdata = element.filter(function (value, index, arr) {
                        const valueTimeString = value.rec_dat + value.rec_tim
                        const valueTime = moment(valueTimeString, 'YYYYMMDDHHmm').valueOf()
                        const endTime = startTime + (8 * 60 * 60 * 1000)
                        return valueTime > startTime && valueTime < endTime
                    })
                    return filterdata
                }
                if (intervalstatus === 0) {
                    const filterdata = element.filter(function (value, index, arr) {
                        const valueTimeString = value.rec_dat + value.rec_tim
                        const valueTime = moment(valueTimeString, 'YYYYMMDDHHmm').valueOf()
                        const endTime = startTime + (1 * 60 * 60 * 1000)
                        return valueTime > startTime && valueTime < endTime
                    })
                    return filterdata
                }
                if (intervalstatus === 1) {
                    const filterdata = element.filter(function (value, index, arr) {
                        const valueTimeString = value.rec_dat + value.rec_tim
                        const valueTime = moment(valueTimeString, 'YYYYMMDDHHmm').valueOf()
                        const endTime = startTime + (4 * 60 * 60 * 1000)
                        return valueTime > startTime && valueTime < endTime
                    })
                    return filterdata
                }
                if (intervalstatus === 2) {
                    const filterdata = element.filter(function (value, index, arr) {
                        const valueTimeString = value.rec_dat + value.rec_tim
                        const valueTime = moment(valueTimeString, 'YYYYMMDDHHmm').valueOf()
                        const endTime = startTime + (8 * 60 * 60 * 1000)
                        return valueTime > startTime && valueTime < endTime
                    })
                    return filterdata
                }
                const filterdata = element.filter(function (value, index, arr) {
                    const valueTimeString = value.rec_dat + value.rec_tim
                    const valueTime = moment(valueTimeString, 'YYYYMMDDHHmm').valueOf()
                    const endTime = startTime + (24 * 60 * 60 * 1000)
                    return valueTime > startTime && valueTime < endTime
                })
                return filterdata
            }
            return []
        }

        function SumforEach(arr) {
            var sum = 0;
            arr.forEach(function (element) {
                sum += Number(element);
            });
            return sum;
        }


        const { Status, IntervalStatus, caseinfo, PickerDate } = this.props

        const timeInterval = pickerTimeArray(PickerDate, IntervalStatus, Status)
        const et = PickerDate + (31 * 60 * 60 * 1000)
        const st = starttime(Status, IntervalStatus, et)
        const WEIGHT = caseinfo.WEIGHT
        const dateWeoght = WardState.selectweight(WEIGHT, PickerDate)

        const QDCLIST = this.state.QDC
        console.log(QDCLIST)
        const Weight = 1000


        var feed = []
        var IV = []
        var Urine = []
        var Other_in = []
        var Other_out = []
        if (QDCLIST !== undefined) {
            if (QDCLIST.length !== 0) {
                var feedarray = []
                var IVarray = []
                var Unarray = []
                var Other_inarray = []
                var Other_outarray = []
                for (let index = 0; index < QDCLIST.length; index++) {
                    const element = QDCLIST[index];
                    const elementTimeString = element.rec_dat + element.rec_tim
                    const elementTime = moment(elementTimeString, 'YYYYMMDDHHmm').valueOf()
                    if (elementTime > st && elementTime < et) {
                        const feedstatus = FiliterReg.Feedarea_Reg_IO().find(function (value, index, array) {
                            return element.item === value
                        })
                        if (feedstatus) {
                            feedarray.push(element)
                        }
                        const IVstatus = FiliterReg.IVarea_reg_IO().find(function (value, index, array) {
                            return element.item === value
                        })
                        if (IVstatus) {
                            IVarray.push(element)
                        }
                        const UOstatus = FiliterReg.UOarea_reg_IO().find(function (value, index, array) {
                            return element.item === value
                        })
                        if (UOstatus) {
                            Unarray.push(element)
                        }
                        const Otherstatus = FiliterReg.Otherarea_reg_IO().find(function (value, index, array) {
                            return element.item === value
                        })
                        if (Otherstatus) {
                            const invalue = element.inp_val1
                            const outvalue = element.out_val1
                            if (invalue !== undefined) {
                                Other_inarray.push(element)
                            }
                            if (outvalue !== undefined) {
                                Other_outarray.push(element)
                            }
                        }
                    }
                }
            }

            for (let index = 0; index < timeInterval.length; index++) {
                const elementTime = timeInterval[index];
                const feedData = IntervalToDataArray(elementTime, Status, IntervalStatus, feedarray)
                feed.push(feedData)
                const IVData = IntervalToDataArray(elementTime, Status, IntervalStatus, IVarray)
                IV.push(IVData)
                const UrineData = IntervalToDataArray(elementTime, Status, IntervalStatus, Unarray)
                Urine.push(UrineData)
                const OtherInData = IntervalToDataArray(elementTime, Status, IntervalStatus, Other_inarray)
                Other_in.push(OtherInData)
                const OtherOutData = IntervalToDataArray(elementTime, Status, IntervalStatus, Other_outarray)
                Other_out.push(OtherOutData)
            }
        }

        const feedmap = feed.map(function (value, index, arr) {
            function SumDataforEach(arr) {
                var sum = 0;
                arr.forEach(function (element) {
                    sum += Number(element.inp_val2);
                });
                return parseInt(sum);
            }
            if (Array.isArray(value)) {
                const data = SumDataforEach(value)
                return data
            }
            return null
        })
        const IVmap = IV.map(function (value, index, arr) {
            function SumDataforEach(arr) {
                var sum = 0;
                arr.forEach(function (element) {
                    sum += Number(element.inp_val1);
                });
                return parseInt(sum);
            }
            if (Array.isArray(value)) {
                const data = SumDataforEach(value)
                return data
            }
            return null
        })

        const Other_inmap = Other_in.map(function (value, index, arr) {
            function SumDataforEach(arr) {
                var sum = 0;
                arr.forEach(function (element) {
                    sum += Number(element.inp_val1);
                });
                return parseInt(sum);
            }
            if (Array.isArray(value)) {
                const data = SumDataforEach(value)
                return data
            }
            return null
        })


        const Urinemap = Urine.map(function (value, index, arr) {
            function SumDataforEach(arr) {
                var sum = 0;
                arr.forEach(function (element) {
                    sum += Number(element.out_val2);
                });
                return parseInt(sum);
            }
            if (Array.isArray(value)) {
                const data = SumDataforEach(value)
                return data
            }
            return null
        })
        console.log(Other_out)
        const Other_outmap = Other_out.map(function (value, index, arr) {
            function SumDataforEach(arr) {
                var sum = 0;
                arr.forEach(function (element) {
                    if (Number(element.out_val2) !== "" || Number(element.out_val2) !== undefined) {
                        if (element.item === "æä¾¿") {
                            sum = sum + 1;
                        }
                        else {
                            sum += Number(element.out_val1);
                        }

                    }
                });
                return parseInt(sum);
            }
            if (Array.isArray(value)) {
                const data = SumDataforEach(value)
                return data
            }
            return null
        })


        if (Status === 'Crossection') {
            feedmap.push(SumforEach(feedmap))
            IVmap.push(SumforEach(IVmap))
            Other_inmap.push(SumforEach(Other_inmap))
            Urinemap.push(SumforEach(Urinemap))
            Other_outmap.push(SumforEach(Other_outmap))
        }




        function suminlist(feedarray, ivarray, otherinarray) {
            var sumarray = []
            for (let index = 0; index < feedarray.length; index++) {
                const feedvalue = feedarray[index];
                const ivvalue = ivarray[index];
                const otherinvalue = otherinarray[index];
                sumarray.push(feedvalue + ivvalue + otherinvalue)
            }
            return sumarray
        }

        function urineunittime(urinemap, weight, status, intervalstatus) {
            var urineunittimemap = []
            if (status === "Crossection") {
                for (let index = 0; index < urinemap.length; index++) {
                    const element = urinemap[index];
                    if (index === 3) {
                        urineunittimemap.push(element / (weight / 1000) / 24)
                    }
                    else {
                        urineunittimemap.push(element / (weight / 1000) / 8)

                    }
                }
            }
            else {
                if (intervalstatus === 0) {
                    for (let index = 0; index < urinemap.length; index++) {
                        const element = urinemap[index];
                        urineunittimemap.push(element / (weight / 1000) / 1)
                    }
                }
                if (intervalstatus === 1) {
                    for (let index = 0; index < urinemap.length; index++) {
                        const element = urinemap[index];
                        urineunittimemap.push(element / (weight / 1000) / 4)
                    }
                }
                if (intervalstatus === 2) {
                    for (let index = 0; index < urinemap.length; index++) {
                        const element = urinemap[index];
                        urineunittimemap.push(element / (weight / 1000) / 8)
                    }
                }
                if (intervalstatus === 3) {
                    for (let index = 0; index < urinemap.length; index++) {
                        const element = urinemap[index];
                        urineunittimemap.push(element / (weight / 1000) / 24)
                    }
                }
            }
            return urineunittimemap
        }


        const suminarray = suminlist(feedmap, IVmap, Other_inmap)
        const urineunit = urineunittime(Urinemap, Weight, Status, IntervalStatus)
        const sumoutarray = suminlist(Urinemap, urineunit, Other_outmap)


        function totalarray(arr1, arr2) {
            var totalarr = []
            for (let index = 0; index < arr1.length; index++) {
                totalarr.push(arr1[index] - arr2[index])
            }
            return totalarr
        }

        const totalarr = totalarray(suminarray, sumoutarray)
        const titleInarray = ['Feed', 'IV', 'Other', 'In:Sum']
        const titleOutarray = ['U/O', 'ml/kg/hr', 'Other', 'Out:Sum']

        const TitleCellIn = titleInarray.map(function (value, index, arr) {
            return (
                <div key={index} style={{ border: "1px solid #cccccc", borderCollapse: "collapse", display: "flex", alignItems: 'center', justifyContent: "center" }}>
                    <t2>{value}</t2>
                </div>
            );
        })

        const TitleCellOut = titleOutarray.map(function (value, index, arr) {
            return (
                <div key={index} style={{ border: "1px solid #cccccc", borderCollapse: "collapse", display: "flex", alignItems: 'center', justifyContent: "center" }}>
                    <t2>{value}</t2>
                </div>
            );
        })

        const Feedcell = this.cellRender(feedmap)
        const IVcell = this.cellRender(IVmap)
        const Other_inmapcell = this.cellRender(Other_inmap)
        const suminarraycell = this.cellRender(suminarray)

        const Urinemapcell = this.cellRender(Urinemap)
        const urineunitcell = this.cellRender(urineunit)
        const Other_outmapcell = this.cellRender(Other_outmap)
        const sumoutarraycell = this.cellRender(sumoutarray)

        const totalcell = this.cellRender(totalarr)

        function stylechange(status) {
            const chronicstyle = "140px 1fr"
            const crossectionstyle = "1fr 4fr"
            if (status === "Crossection") {
                return crossectionstyle
            }
            else return chronicstyle
        }
        return (
            <div>
                <div style={{ display: "grid", gridTemplateColumns: stylechange(Status) }}>
                    <div style={{ display: "grid", gridTemplateRows: "40px 40px 40px 40px" }}>
                        {TitleCellIn}
                    </div>
                    <div style={{ display: "grid", gridTemplateRows: "40px 40px 40px 40px" }}>
                        {Feedcell}
                        {IVcell}
                        {Other_inmapcell}
                        {suminarraycell}
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: stylechange(Status) }}>
                    <div style={{ display: "grid", gridTemplateRows: "40px 40px 40px 40px" }}>
                        {TitleCellOut}
                    </div>
                    <div style={{ display: "grid", gridTemplateRows: "40px 40px 40px 40px" }}>
                        {Urinemapcell}
                        {urineunitcell}
                        {Other_outmapcell}
                        {sumoutarraycell}
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: stylechange(Status) }}>
                    <div style={{ border: "1px solid #cccccc", borderCollapse: "collapse", display: "flex", alignItems: "center", justifyContent: "center" }}>
                        <t2>I-O</t2>
                    </div>
                    {totalcell}
                </div>
            </div>
        );
    }

}

export default IOpage;