import React, { Component } from 'react'
import rtlogo from '../../Image/svg/RT.svg'
import { rtoneday, rethird } from '../../Data/Wardviewdata'
import { arraychange } from '../../Function/Wardstate'
import { Tooltip } from 'antd'
import * as FilterReg from '../../Function/FiliterReg'
import moment from 'moment'

class Chronicrttable extends Component {
    // 0:ä¸å¤© 1:ä¸å¤© 2:ä¸é± 3:å©é±
    switch_state() {
        const { chronic_state } = this.props
        switch (chronic_state) {
            case 0:
                return 24
            case 1:
                return 21
            case 2:
                return 21
            case 3:
                return 14
            default:
                break;
        }
    }
    switch_state_title() {
        return "140px"
    }

    rendertooltip() {
        return (
            <div>123</div>
        );
    }
    table_content() {
        const { chronic_state, QSPE, PickerDate } = this.props
        const DataList = FilterReg.QSPERegList(QSPE)
        const st = PickerDate + 31 * 60 * 60 * 1000 - 24 * 60 * 60 * 1000
        var RTList = []
        if (chronic_state === 0) {
            for (let index = 0; index < 24; index++) {
                const start = st + (index * 60 * 60 * 1000)
                const end = st + ((index + 1) * 60 * 60 * 1000)
                const filterData = DataList.filter(function (value, index, arr) {
                    const time = moment(value.Date.toString() + value.Time.toString(), "YYYYMMDDHHmm").valueOf()
                    return start < time && time < end
                })
                RTList.push({
                    length: filterData.length,
                    element: filterData
                })

            }
        }
        if (chronic_state === 1) {
            for (let index = 0; index < 18; index++) {
                const start = st + (index * 4 * 60 * 60 * 1000)
                const end = st + ((index + 1) * 4 * 60 * 60 * 1000)
                const filterData = DataList.filter(function (value, index, arr) {
                    const time = moment(value.Date.toString() + value.Time.toString(), "YYYYMMDDHHmm").valueOf()
                    return start < time && time < end
                })
                RTList.push({
                    length: filterData.length,
                    element: filterData
                })

            }
        }
        if (chronic_state === 2) {
            for (let index = 0; index < 21; index++) {
                const start = st + (index * 8 * 60 * 60 * 1000)
                const end = st + ((index + 1) * 8 * 60 * 60 * 1000)
                const filterData = DataList.filter(function (value, index, arr) {
                    const time = moment(value.Date.toString() + value.Time.toString(), "YYYYMMDDHHmm").valueOf()
                    return start < time && time < end
                })
                RTList.push({
                    length: filterData.length,
                    element: filterData
                })

            }
        }
        if (chronic_state === 3) {
            for (let index = 0; index < 14; index++) {
                const start = st + (index * 24 * 60 * 60 * 1000)
                const end = st + ((index + 1) * 24 * 60 * 60 * 1000)
                const filterData = DataList.filter(function (value, index, arr) {
                    const time = moment(value.Date.toString() + value.Time.toString(), "YYYYMMDDHHmm").valueOf()
                    return start < time && time < end
                })
                RTList.push({
                    length: filterData.length,
                    element: filterData
                })

            }
        }
        if (RTList === null || RTList === undefined) {
            return null
        }
        if (RTList.length === 0) {
            return null
        }
        const cellRender = RTList.map(function (value, index, arr) {
            if (value.length === 0) {
                return (
                    <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }} key={index}>
                        {changetext(value.length)}
                    </centerstyle>
                );
            }
            else {
                return (
                    <Tooltip placement="right" autoAdjustOverflow={true} overlayStyle={{ background: "white" }} title={"123"}>
                        <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }} key={index}>
                            {changetext(value.length)}
                        </centerstyle>
                    </Tooltip>
                );
            }
        })


        function changetext(text) {
            if (text === 0) {
                return ''
            }
            return <img src={rtlogo} alt="rtlogo" />
        }
        return cellRender
    }
    render() {

        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px 1fr", height: "40px" }}>
                <div style={{ backgroundColor: "#cccccc", display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <t2>å¼å¸è©ä¼°</t2>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + this.switch_state() + ",1fr)" }}>
                    {this.table_content()}
                </div>
            </div>
        );
    }
}

export default Chronicrttable