import React, { Component } from 'react'
import { DatePicker, Input } from 'antd';
import searchlogo from '../../Image/svg/Search.svg'
import Nurtiontpncell from './Nurtion_TPN_cell'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import * as ApiGithub from '../../API/ApiGithub'

class Nurtiontpnorder extends Component {
    state = {
        onEdit: false,
        tpndata: [],

        PickerDate: moment(moment().format('YYYYMMDD'), 'YYYYMMDD').valueOf(),
        UDORDER0: [],
        tpnParser: []
    }
    componentDidMount() {
        const getAPITPNData = this.getAPITPNData.bind(this)
        ApiGithub.getGitHubJson("TpnOrder.json")
        .then(data => {
            let parser = data.data.find(item => item.apply_condition.name === "TPN Order");
            console.log("getGitHubJson TPN Order OK " + JSON.stringify(parser))
            this.setState({
                tpnParser: parser
            }, () => {
                getAPITPNData()
            })
        })
        .catch(err => {
          console.log(err)
        })
        
    }
    getAPITPNData() {
        const { caseinfo } = this.props
        const HID = caseinfo.HISID.toString().trim()
        const CID = caseinfo.CASEID.toString().trim()
        const { tpnParser } = this.state
        // console.log(HID, CID)
        ApiProvider.getStoredProcedureUDORDER0(HID, CID, "123")
            .then(data => {
                //create list need to do search
                let tpnList = [] // All TPN data
                data.data.forEach(item => {
                    const found = item.UDDRGNAM.match(RegExp("Ampicillin\\s+"))
                    if(found !== null && found !== "") tpnList.push(item)
                });

                //parser 7 value from each TPN document ï¼AA, Glu, Na, K, Ca, P, Mgï¼
                tpnList.forEach((item, index) => {
                    ApiProvider.getStoredProcedureUDTEXTQ1(CID, item.UDORDSEQ)
                    .then(data => {
                        console.log("getStoredProcedureUDTEXTQ1 OK " + JSON.stringify(data.data))

                        //parse every line
                        const result = this.parse(data.data, tpnParser)
                        item.TPNDATA = result;
                        tpnList.splice(index, 1, item)
                        this.setState({
                            tpndata: tpnList
                        })
                    })
                    .catch(err => {
                        console.log("getStoredProcedureUDTEXTQ1 NG " + err)
                    });
                });             
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureUDORDER0 NG " + err)
            });
    }
    parse = (inputs, parser) => {
        let mappings = parser.mappings;
        let allResult = {};
        mappings.forEach(mapping => {
          allResult[mapping.key] = ""
        });

        inputs.forEach(input => {
            let content = input.UDWORD;
            mappings.forEach(mapping => {
                const re = new RegExp(mapping.regex_str, mapping.regex_flags)
                const match = content.match(re)
                if(match !== null && match[0] !== "") {
                    allResult[mapping.key] = match[mapping.position]
                }
            });
        });
        return allResult;
    }
    Edit_state() {
        this.setState(
            {
                onEdit: true
            }
        )
    }
    handleOk = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };

    render() {
        // console.log("123")
        const { caseinfo } = this.props
        const title = ["éå§æé", "çµææé", "çæ", "èç½®åç¨±"]
        const titilecell = title.map(function (info, index) {
            return (
                <div key={index} style={{ padding: "0px 5px", display: "flex", alignItems: "center" }}>
                    <t4>{info}</t4>
                </div>
            );
        })
        const ListCellData = this.state.tpndata
        const filterData = ListCellData.filter((element, index, arr) => {
            const time = this.state.PickerDate
            const starttime = moment(moment(element.UDBGNDT, 'YYYY-MM-DD-HH.mm.ss.000000').format("YYYYMMDD"), "YYYYMMDD").valueOf()
            // const endtime = moment(element.UDENDDT, 'YYYY-MM-DD-HH.mm.ss.000000').valueOf()
            return starttime === time
        })
        const DataCellRender = filterData.map(function (element, index, arr) {
            return (
                <Nurtiontpncell key={index} data={element} caseinfo={caseinfo} ></Nurtiontpncell>
            );
        })
        console.log(ListCellData)
        return (
            <div>
                <t1>TPN order</t1>
                <div style={{ display: "grid", gridTemplateColumns: "220px 1fr 25px", gridColumnGap: "10px" }}>
                    <DatePicker defaultValue={moment(this.state.PickerDate)} allowClear={false} onChange={(date, datestring) => { this.setState({ PickerDate: date.valueOf() }) }} />
                    {/* <Input id="nurtion_feeding_input" placeholder="è«è¼¸å¥ééµå­" />
                    <div onClick={() => this.search_click()} style={{ display: "flex", alignItems: "center" }}>
                        <img src={searchlogo} alt="searchlogo" />
                    </div> */}
                </div>
                <div style={{ display: "grid", gridTemplateColumns: " 1fr 1fr 60px 450px", background: "#cccccc", height: "50px", marginTop: "10px" }}>
                    {titilecell}
                </div>
                <div style={{ minHeight: "120px", maxHeight: "400px", overflowY: "auto" }}>
                    {DataCellRender}
                </div>
            </div>
        );
    }
}

export default Nurtiontpnorder
