import React, { Component } from 'react'
import Chronicrttable from './Chronic_rttable'

class Rtonchronic extends Component {
    render() {
        const { title, userdata, select_interval, QSPE, PickerDate } = this.props
        const item_title = [{ title: "RT" }]
        return (
            <div style={{ marginTop: "15px" }}>
                <Chronicrttable PickerDate={PickerDate} QSPE={QSPE} title={item_title} userdata={userdata} chronic_state={select_interval}></Chronicrttable>
            </div>
        );
    }
}

export default Rtonchronic