import React, { Component } from 'react'
import './Chronic_table.scss'
import moment from 'moment'
import * as RegularData from '../../Data/RegularData'
import * as ApiGithub from '../../API/ApiGithub'

class Tableonchronic extends Component {
    state = {
        LabParse: RegularData.ChronicLab()
    }
    componentDidMount() {
        ApiGithub.getGitHubJson("Chronic_Lab.json")
            .then(data => {
                this.setState({
                    LabParse: data.data
                })
            })
            .catch(err => {
                console.log(err)
            })
    }
    // 0:ä¸å¤© 1:ä¸å¤© 2:ä¸é± 3:å©é±
    switch_state() {
        const { chronic_state } = this.props
        switch (chronic_state) {
            case 0:
                return 24
            case 1:
                return 18
            case 2:
                return 21
            case 3:
                return 14
            default:
                break;
        }
    }
    switch_state_title() {
        return "140px"
    }
    render() {
        function datareglist(ressect, interval, pickerdate) {
            function starttime(i, d) {
                if (i === 0) {
                    return new Date(d).getTime() + (7 * 60 * 60 * 1000)
                }
                if (i === 1) {
                    return new Date(d).getTime() + (31 * 60 * 60 * 1000) - (3 * 24 * 60 * 60 * 1000)
                }
                if (i === 2) {
                    return new Date(d).getTime() + (31 * 60 * 60 * 1000) - (7 * 24 * 60 * 60 * 1000)
                }
                if (i === 3) {
                    return new Date(d).getTime() + (31 * 60 * 60 * 1000) - (14 * 24 * 60 * 60 * 1000)
                }
                return null
            }
            function TimeInterval(st, interval) {
                var timearray = []
                if (interval === 0) {
                    for (let index = 0; index < 24; index++) {
                        const time = st + (index * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
                if (interval === 1) {
                    for (let index = 0; index < 18; index++) {
                        const time = st + (index * 4 * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
                if (interval === 2) {
                    for (let index = 0; index < 21; index++) {
                        const time = st + (index * 8 * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
                if (interval === 3) {
                    for (let index = 0; index < 14; index++) {
                        const time = st + (index * 24 * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
                return timearray
            }
            function TimeArrayInterval(interval) {
                if (interval === 0) {
                    return (1 * 60 * 60 * 1000)
                }
                if (interval === 1) {
                    return (4 * 60 * 60 * 1000)
                }
                if (interval === 2) {
                    return (8 * 60 * 60 * 1000)

                }
                if (interval === 3) {
                    return (24 * 60 * 60 * 1000)
                }
                return null
            }
            const et = new Date(pickerdate).getTime() + (31 * 60 * 60 * 1000)
            const st = starttime(interval, pickerdate)
            const IntervalArray = TimeInterval(st, interval)
            var RessectForPickerDate = []
            if (Array.isArray(ressect)) {
                for (let index = 0; index < ressect.length; index++) {
                    const element = ressect[index];
                    const time = element.ORRCPDT.toString() + element.ORRCPTM.toString()
                    const timestamp = moment(time, 'YYYY-MM-DDHH:mm:ss').valueOf()
                    if (timestamp > st && timestamp < et) {
                        RessectForPickerDate.push(element)
                    }
                }
            }
            var FormatTimeRessect = []
            for (let index = 0; index < IntervalArray.length; index++) {
                const si = IntervalArray[index];
                const ei = si + TimeArrayInterval(interval);
                const array = RessectForPickerDate.filter(function (value, index, array) {
                    const time = value.ORRCPDT.toString() + value.ORRCPTM.toString()
                    const timestamp = moment(time, 'YYYY-MM-DDHH:mm:ss').valueOf()
                    return timestamp > si && timestamp < ei
                })
                FormatTimeRessect.push(array)
            }
            return FormatTimeRessect
        }

        function DataLength(datalist) {
            var array = []
            for (let index = 0; index < datalist.length; index++) {
                const element = datalist[index];
                array.push(element.length)
            }
            return array
        }

        function LabFormat(DataList, parsrList) {
            var arrayBiochemical = []
            var arrayBlood = []
            var arrayCultivate = []
            var arrayGAS = []
            var arrayGlu = []
            var arrayRadiation = []
            var arrayOther = []
            if (Array.isArray(DataList)) {
                if (DataList.length === 0) {
                    return null
                }
                for (let index = 0; index < DataList.length; index++) {
                    const element = DataList[index];
                    const name = element.ORPROCED
                    // console.log(parsrList.Biochemical)
                    const BioFind = parsrList.Biochemical.some(function (value, index, arr) {
                        return name.match(value) !== null
                    })
                    const BloFind = parsrList.Blood.some(function (value, index, arr) {
                        return name.match(value) !== null
                    })
                    const CulFind = parsrList.Cultivate.some(function (value, index, arr) {
                        return name.match(value) !== null
                    })
                    const GasFind = parsrList.GAS.some(function (value, index, arr) {
                        return name.match(value) !== null
                    })
                    const GluFind = parsrList.GAS.some(function (value, index, arr) {
                        return name.match(value) !== null
                    })
                    const RadFind = parsrList.Radiation.some(function (value, index, arr) {
                        return name.match(value) !== null
                    })
                    if (BioFind) {
                        arrayBiochemical.push(element)
                    }
                    if (BloFind) {
                        arrayBlood.push(element)
                    }
                    if (CulFind) {
                        arrayCultivate.push(element)
                    }
                    if (GasFind) {
                        arrayGAS.push(element)
                    }
                    if (GluFind) {
                        arrayGlu.push(element)
                    }
                    if (RadFind) {
                        arrayRadiation.push(element)
                    }
                    if (!BioFind && !BloFind && !CulFind && !GasFind && !RadFind) {
                        arrayOther.push(element)
                    }
                }
            }
            console.log([arrayBiochemical, arrayBlood, arrayCultivate, arrayGAS, arrayRadiation, arrayOther])
            return [arrayBiochemical, arrayBlood, arrayCultivate, arrayGAS, arrayGlu, arrayRadiation, arrayOther]
        }

        const { chronic_state, RESSECT, caseinfo, PickerDate, title } = this.props
        const title_list = title.map((info, index) => <div className='Chronictabletitlelist' key={index}><t2>{info.title}</t2></div>)


        const data = LabFormat(RESSECT, this.state.LabParse)

        const cellMap = data.map(function (value, index, arr) {
            const DataList = datareglist(value, chronic_state, PickerDate)
            const ListLength = DataLength(DataList)
            return ListLength
        })
        console.log(cellMap)
        // const DataList = datareglist(RESSECT, chronic_state, PickerDate)
        // const ListLength = DataLength(DataList)
        const statestatus = this.switch_state()
        const cell = cellMap.map(function (value, index, array) {
            const valuecell = value.map(function (e, i, a) {
                return (
                    <centerstyle style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: 'collapse' }} key={index}>
                        <t2>{e}</t2>
                    </centerstyle>
                );
            })
            return valuecell
        })
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px 1fr" }}>
                <div style={{ display: "grid", gridTemplateRows: "repeat(" + title.length + ",1fr)" }}>
                    {title_list}
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "repeat(" + statestatus + ",1fr)", gridTemplateRows: "repeat(7,1fr)" }}>
                    {cell}
                </div>
            </div>
        );
    }
}

export default Tableonchronic