import React, { Component } from 'react';
import Flootsheetio from './Flootsheetio'
// import Flowsheetvitalsign from './Flootsheetvitalsign'
import Vitalxaxis from '../../Module/Vitalxaxis'
import { DatePicker } from 'antd';
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import './Flootsheetlayout.scss'
import X2JS from 'x2js';
import * as Wardstate from '../../Function/Wardstate'

class Flowsheetlayout extends Component {
    state = {
        PickerDate: moment(moment().format('YYYYMMDD')).valueOf(),
        QDC: [],
        QSPE: [],
        // itemlist: []
    }
    componentDidMount() {
        // const itemarray = ['10:00 è³æè¡å®¤æè¡é', '13:50 è³æè¡å®¤è§å¯å', '14:30 è¿åçæ¿']
        // this.setState({
        //     itemlist: itemarray
        // })
        this.ApiData(this.state.PickerDate)
    }

    ApiData() {
        const DateTime = this.state.PickerDate
        const xm2 = new X2JS()
        const { caseinfo } = this.props
        const st = moment(DateTime + (7 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        const et = moment(DateTime + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        console.log(caseinfo)
        const QDC = ApiProvider.getQDC(caseinfo.CASEID, st, et)
            .then(data => {
                console.log(xm2.xml2js(data.data))
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                return parsedata
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const QSPE = ApiProvider.getQSPE(caseinfo.CASEID, st, et)
            .then(data => {
                const dataJson = Wardstate.XM2JSON(data.data)
                // const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                if (Array.isArray(dataJson)) {
                    return dataJson
                }
                else {
                    const arr = []
                    arr.push(dataJson)
                    return arr
                }
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        Promise.all([QDC, QSPE]).then(result => {
            console.log("QSPE DATA = " + result[1])
            this.setState({
                QDC: result[0], //IOè¦ç¨ç
                QSPE: result[1]
            })
        })
    }
    onDateChange = (date, dateString) => {
        this.sendDateData(date.valueOf())
    }

    render() {
        const data = this.state.QSPE || [] //ç¹æ®è­·çäºä»¶
        const array = this.state.QDC || []
        const splist = data.filter(function (value, index, array) {
            if (value === undefined) {
                return null
            }
            console.log(value)
            const str = value.special
            return str.match("@v") === null
        })
        const specialListRender = splist.map(function (value, index, arr) {
            return (
                <div key={index} style={{ display: "flex", color: "rgba(59, 151, 225, 1)" }}>{value.special.toString().trim()}</div>
            );
        })
        if (array.length === 0) {
            return null
        }
        return (
            <div style={{ paddingLeft: "20px", paddingRight: "20px" }}>
                <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr 6fr" }}>
                    <div style={{ display: "grid", gridTemplateRows: "1fr 1fr 1fr" }}>
                        <div style={{ padding: "0px 10px" }}>
                            <DatePicker width={200} defaultValue={moment(this.state.PickerDate)} allowClear={false} onChange={this.onDateChange} />
                        </div>
                    </div>
                    <div style={{ display: "grid", gridTemplateRows: "1fr 1fr 1fr" }}>
                        <t2 style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                            ç¹æ®è­·çäºä»¶:<t2 style={{ width: "20px", height: "20px", borderRadius: "99em", background: "black", color: "white", display: "flex", justifyContent: "center", alignItems: 'center' }}>{splist.length}</t2>
                        </t2>
                    </div>
                    <div style={{ padding: "5px 10px" }}>
                        <div style={{ border: "rgba(238,238,238,1) 1px solid", borderRadius: "4px", maxHeight: "100px", height: "100px", overflow: "auto" }}>
                            {specialListRender}
                        </div>
                    </div>
                </div>
                <div style={{ marginBottom: "10px" }}>
                    <div style={{ display: "grid", gridTemplateColumns: "90px 1fr", height: "10px" }}>
                        <div></div>
                        <Vitalxaxis max={200} min={0} select_interval={0} select_date={this.state.PickerDate} id_key="xaxis_flootsheet" svg_id="xaxis_svg_flootsheet"></Vitalxaxis>
                    </div>
                </div>
                <div className='boxshadow'></div>
                <div className='flootsheetlayout'>
                    <Flootsheetio PickerDate={this.state.PickerDate} QDC={this.state.QDC}></Flootsheetio>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "90px 1fr", height: "10px" }}>
                    <div></div>
                    <Vitalxaxis max={200} min={0} select_interval={0} select_date={this.state.PickerDate} id_key="xaxis_flootsheet_bot" svg_id="xaxis_svg_flootsheet_bot"></Vitalxaxis>
                </div>
            </div>
        );
    }
}

export default Flowsheetlayout;