import React, { Component } from 'react';
import './Nonlabviewtable.scss'
import NonlabviewtableCell from './NonlabviewtableCell'
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider'
import { Modal } from 'antd';
import InsReportModal from '../ExamReportModal'

class Nonlabviewtable extends Component {
    state = {
        visible: false,

        modalVisible: false,
        modalReport: "",
        ORDLIST: [],
        RESSECT: []
    }
    componentDidMount() {
        this.nonLabApiGet()
    }
    nonLabApiGet() {
        function selectMonthInterval(storeProcedureTime) {
            const timestampInterval = moment() - moment(storeProcedureTime, "YYYY-MM-DD").valueOf()
            const timestampMonth = 30 * 24 * 60 * 60 * 1000
            const MonthInt = parseInt(timestampInterval / timestampMonth) + 1
            return MonthInt
        }
        const { caseinfo } = this.props
        const storeProcedureTime = moment(caseinfo.TRANSINDT, "YYYYMMDD").format("YYYY-MM-DD")
        const MonthInterval = selectMonthInterval(storeProcedureTime)
        ApiProvider.getStoredProcedureORDLIST(caseinfo.HISID, storeProcedureTime, "ALL", "DOC3924B")
            .then(data => { this.setState({ ORDLIST: data.data }) })
            .catch(err => { console.log("getStoredProcedureORDLIST NG " + JSON.stringify(err)) });
        ApiProvider.getStoredProcedureRESSECT(caseinfo.HISID, MonthInterval, "ALL", "DOC3924B")
            .then(data => { this.setState({ RESSECT: data.data }) })
            .catch(err => { console.log("getStoredProcedureRESSECT NG " + JSON.stringify(err)) });
    }
    showModal = (report) => {
        console.log("showModal :" + JSON.stringify(report))
        this.setState({
            modalVisible: true,
            modalReport: report
        });
    };

    handleOk = e => {
        this.setState({
            modalVisible: false,
        });
    };

    handleCancel = e => {
        this.setState({
            modalVisible: false,
        });
    };
    render() {
        function listforstatus(ordlist, ressect, StartTime, EndTime) {
            var status_report = []
            // console.log(ordlist, ressect, pickerdate)
            const endTime = EndTime
            const startTime = StartTime
            if (Array.isArray(ordlist) && Array.isArray(ressect)) {
                for (let index = 0; index < ordlist.length; index++) {
                    const element = ordlist[index];
                    const time = moment(element.ORBGNDT.toString().trim() + element.ORBGNTM.toString().trim(), "YYYY-MM-DDHH:mm:ss").valueOf()
                    console.log(time, startTime, endTime)
                    if (time >= startTime && time <= endTime) {
                        const status = ressect.find(function (value, index, array) {
                            return value.ORDSEQNO === element.ORDSEQNO
                        })
                        if (status) {
                            status_report.push({
                                "status": true,
                                "element": element
                            })
                        }
                        else {
                            status_report.push({
                                "status": false,
                                "element": element
                            })

                        }
                    }
                }
            }
            return status_report
        }
        const { modalVisible, modalReport, ORDLIST, RESSECT } = this.state
        const { StartTime, EndTime } = this.props
        const list = listforstatus(ORDLIST, RESSECT, StartTime, EndTime)

        const showModal = this.showModal
        const handleOk = this.handleOk
        const handleCancel = this.handleCancel
        const nonlabcell = list.map(function (value, index, array) {
            console.log(value.element)
            const come = value.element.ORSPCN1;
            const time = moment(value.element.ORBGNDT.toString().trim() + value.element.ORBGNTM.toString().trim(), "YYYY-MM-DDHH:mm:ss").valueOf();
            const name = value.element.ORPROCED;
            const status = value.status;
            return (
                <NonlabviewtableCell key={"Nonlabviewtable" + index} come={come} time={time} name={name} statis={status} hisId={value.element.ORHISTNO} caseId={value.element.ORDSEQCN} month={value.element.ORMM} seq={value.element.ORDSEQNO} showModal={showModal}></NonlabviewtableCell>
            );
        })
        return (
            <div>
                <div style={{ height: "50px", border: "#E2EDF3 1px solid", backgroundColor: "#E2EDF3", display: "grid", gridTemplateColumns: "1fr 1.5fr 1fr 1fr 1fr" }}>
                    <t3 className='nonlabtebletitlecell'>æª¢é©æ¥æ</t3>
                    <t3 className='nonlabtebletitlecell'>æª¢é©é ç®åç¨±</t3>
                    <t3 className='nonlabtebletitlecell'>æª¢é«ä¾æº</t3>
                    <t3 className='nonlabtebletitlecell'>æª¢é©é ç®çæ</t3>
                    <t3 className='nonlabtebletitlecell'>æª¢é©çµæ</t3>
                </div>
                <div className='nonlabviewtable'>
                    {nonlabcell}
                </div>
                <Modal
                    title={"æª¢é©çµæ"}
                    visible={modalVisible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    width={1000}
                    footer={null}>
                    <InsReportModal report={modalReport}></InsReportModal>
                </Modal>
            </div>
        );
    }
}

export default Nonlabviewtable
