import React, { Component } from 'react';
import Prescriptionordertable from './Prescriptionordertable'
import { DatePicker, Select, Input, Button } from 'antd';
import searchlogo from '../../Image/svg/Search.svg'
import './Prescriptionorder.scss'
import moment from 'moment'
import * as ApiGithub from '../../API/ApiGithub'

// var DateRange, SelectData
class Prescriptionorderlayout extends Component {
    state = {
        InputMode: null,
        whiteListParser: undefined,
        classParser: { "mappings": [] },
        DateRangeMode: {},
        class: "å¨é¨",
        SelectMode: 0,
        search: false
    }
    componentDidMount() {
        ApiGithub.getGitHubJson("WhiteList.json")
            .then(data => {
                let parser = data.data;
                this.setState({
                    whiteListParser: parser
                })
            })
            .catch(err => {
                console.log(err)
            })

        ApiGithub.getGitHubJson("Class.json")
            .then(data => {
                let parser = data.data.find(item => item.apply_condition.name === "Prescription");
                parser.mappings = [{ "key": "å¨é¨", "regex_str": null }, ...parser.mappings]
                console.log("getGitHubJson Prescription OK " + JSON.stringify(parser))
                this.setState({
                    classParser: parser
                })
            })
            .catch(err => {
                console.log(err)
            })
    }

    onDateRangeChange = (date, dateString) => {
        let timeRange = {
            'st': date[0].valueOf(),
            'et': date[1].valueOf()
        }
        this.setState({
            DateRangeMode: timeRange
        })
    }

    onChangeClass = (value) => {
        this.setState({
            class: value
        })
    }

    handleChange = (value) => {
        this.setState({
            SelectMode: value
        })
    }

    onClickSearch = (e) => {
        const inputdata = document.querySelector("#prescriptionorderinput").value

        // if (inputdata === undefined || inputdata === " " || inputdata === null) {
        //     return null
        // }
        // if (sT === undefined || sT === null || sT === " " || eT === undefined || eT === null || eT === " ") {
        //     return null
        // }

        this.setState({
            // SelectMode: SelectData,
            InputMode: inputdata,
            // DateRangeMode: DateRange
        })

    }

    select_parser = (input, parser) => {
        if (parser === undefined) return undefined;
        return parser.find(x =>
            input.match(x.apply_condition.name)
        )
    }

    parse = (inputs, parser) => {
        let mappings = parser.mappings;
        let allResult = {
            "white": [],
            "black": []
        };
        const UDGNAME_condition = mappings.find(item => item.key === "UDGNAME");
        const UDRPNAME_condition = mappings.find(item => item.key === "UDRPNAME");
        inputs.forEach(input => {
            const UDGNAME_re = new RegExp(UDGNAME_condition.regex_str, UDGNAME_condition.regex_flags)
            const UDGNAME_match = input.UDGNAME.match(UDGNAME_re)

            const UDRPNAME_re = new RegExp(UDRPNAME_condition.regex_str, UDRPNAME_condition.regex_flags)
            const UDRPNAME_match = input.UDRPNAME.match(UDRPNAME_re)

            if (UDGNAME_match !== null && UDRPNAME_match !== null) {
                allResult["white"].push(input);
            } else {
                allResult["black"].push(input);
            }
        });

        return allResult;
    }

    parseClass = (inputs, parser) => {
        let mappings = parser.mappings;
        let allResult = {};
        mappings.forEach(mapping => {
            allResult[mapping.key] = []
        });
        allResult["å¨é¨"] = []

        inputs.forEach(input => {
            let content = input.UDGNAME;
            mappings.forEach(mapping => {
                const re = new RegExp(mapping.regex_str, mapping.regex_flags)
                const match = content.match(re)
                if (match !== null && match[0] !== "") {
                    allResult[mapping.key].push(input)
                }
            });

            allResult["å¨é¨"].push(input)
        });
        return allResult;
    }

    ControlBarRender() {
        const { RangePicker } = DatePicker;
        const { Option } = Select;
        return (

            <div class="searchBar" style={{ display: "grid", gridTemplateColumns: "220px 130px 110px 150px auto", gridColumnGap: "10px", marginBottom: "5px" }}>
                <RangePicker placeholder={["è«è¼¸å¥æ¥æ", "è«è¼¸å¥æ¥æ"]} onChange={this.onDateRangeChange} allowClear={false} />
                <Select defaultValue={this.state.class} style={{ width: 130 }} onChange={this.onChangeClass}>
                    {this.state.classParser.mappings.map((mapping, index) => {
                        return <Option value={mapping.key}>{mapping.key}</Option>
                    })}
                </Select>
                <Select defaultValue={this.state.SelectMode} style={{ width: 110 }} onChange={this.handleChange}>
                    <Option value={0}>å¨</Option>
                    <Option value={1}>çæä¸­</Option>
                    <Option value={2}>å¤±æ/å·²çµæ/åæ¶</Option>
                </Select>
                <Input id="prescriptionorderinput" placeholder="è¼¸å¥è¥åééµå­" />
                <Button onClick={this.onClickSearch.bind(this)} shape="circle" icon="search" style={{ display: "flex", justifyContent: "center", alignItems: "center" }} />
                {/* <div  onClick={this.onClickSearch} style={{ display: "flex", justifyContent: "center", alignItems: "center", color: "blue", textDecoration: "underline", cursor: "pointer" }}>
                    <img src={searchlogo} alt="searchlogo" />
                </div> */}
            </div >
        );
    }

    SortDataList = (DataList) => {
        console.log(JSON.stringify(DataList))
        const { searchbar, PickerDate } = this.props
        const { InputMode, SelectMode, DateRangeMode } = this.state

        if (SelectMode !== 0) {
            console.log(SelectMode)
            /*
            é è¨­
            å¨é¨ SelectMode == 0
            å·²çæ SelectMode == 1, < ç¾å¨æé <
            å¤±æ SelectMode == 2, < ç¾å¨æé
            */
            DataList = DataList.filter(data => {
                const createST = moment(data.UDBGNDT.toString().trim(), 'YYYY-MM-DD-HH.mm.ss.000000').valueOf()
                const createET = moment(data.UDENDDT.toString().trim(), 'YYYY-MM-DD-HH.mm.ss.000000').valueOf()
                const nowT = moment().valueOf()

                switch (SelectMode) {
                    case 1:
                        return createST <= nowT && nowT <= createET
                        break;
                    case 2:
                        console.log(createET, nowT)
                        return createET < nowT
                        break;
                    default: return null;
                }
            })
        }

        if (InputMode !== null) {
            console.log(InputMode)
            DataList = DataList.filter(data => {
                return data.UDGNAME.match(InputMode) !== null || data.UDRPNAME.match(InputMode) !== null
            })
        }

        var DataFormat = []
        const et = searchbar ? DateRangeMode.et : PickerDate + (31 * 60 * 60 * 1000)
        const st = searchbar ? DateRangeMode.st : et - (3 * 24 * 60 * 60 * 1000)
        for (let index = 0; index < DataList.length; index++) {
            const element = DataList[index];
            const time = moment(element.UDBGNDT.toString().trim(), 'YYYY-MM-DD-HH.mm.ss.000000').valueOf()
            if (st <= time && time <= et) {
                DataFormat.push(element)
            }
        }
        return DataFormat
    }

    render() {
        const { searchbar, UDORDER0, caseinfo } = this.props
        const { SelectMode } = this.state

        const controlBar = this.ControlBarRender()

        const parseClass = this.parseClass;

        const selected_parser = this.select_parser("Prescription", this.state.whiteListParser)

        let ListData = [];
        if (selected_parser !== undefined) {
            const whiteBlackList = this.parse(UDORDER0, selected_parser)
            console.log("whiteBlackList " + JSON.stringify(whiteBlackList));

            let clusteredData = {}
            if (this.state.classParser !== undefined) {
                clusteredData = parseClass(whiteBlackList["white"], this.state.classParser)
            }
            console.log("clusteredData " + JSON.stringify(clusteredData));

            //filter data by selected class
            let filteredData = clusteredData[this.state.class];

            ListData = this.SortDataList(filteredData)
        }

        return (
            <div>
                <div className={searchbar ? 'PrescriptionOrderSearchBar' : 'PrescriptionOrderNonSearchBar'}>
                    <t1>Prescription Order</t1>
                </div>
                {searchbar ? controlBar : null}
                <Prescriptionordertable caseinfo={caseinfo} UDORDER0={ListData} SelectMode={SelectMode}></Prescriptionordertable>
            </div>
        );
    }
}

export default Prescriptionorderlayout
