import React, { Component } from 'react';
import GAS from '../CrossSectionView/GAS'
import RTbreathe from '../CrossSectionView/RTbreathe'
import './RTview.scss'
import X2JS from 'x2js';
import * as ApiProvider from '../../API/ApiProvider';
import moment from 'moment'

class RTview extends Component {
    state = {
        RESDBGAS: [],
        QSPE: [],
        PickerDate: moment(moment().format('YYYYMMDD')).valueOf()
    }
    componentDidMount() {
        this.API_DATA(this.state.PickerDate)
    }
    API_DATA(Datetime) {
        function selectMonthInterval(PickerDate) {
            const timestampInterval = new Date().getTime() - new Date(PickerDate).getTime()
            const timestampMonth = 30 * 24 * 60 * 60 * 1000
            const MonthInt = parseInt(timestampInterval / timestampMonth) + 1
            return MonthInt
        }
        const xm2 = new X2JS()
        const { caseinfo } = this.props
        const MonthInterval = selectMonthInterval(Datetime)
        const st = moment(Datetime + (7 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        const et = moment(Datetime + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        const TRANSDate = moment(caseinfo.TRANSINDT, 'YYYYMMDD').format('YYYY-MM-DD')
        ApiProvider.getStoredProcedureRESDBGAS(caseinfo.HISID, TRANSDate)
            .then(data => {
                console.log(data.data)
                this.setState({
                    RESDBGAS: data.data
                })
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        // const QSPE = ApiProvider.getQSPE(caseinfo.CASEID, st, et)
        ApiProvider.getQSPE(caseinfo.CASEID, st, et)
            .then(data => {
                console.log(xm2.xml2js(data.data).outmsg.resconts.rescont)
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                console.log(parsedata)
                if (Array.isArray(parsedata)) {
                    this.setState({
                        QSPE: parsedata
                    })
                }
                else {
                    const arr = []
                    arr.push(parsedata)
                    this.setState({
                        QSPE: arr
                    })
                }
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
    }
    render() {
        const { caseinfo } = this.props
        const RESDBGAS = this.state.RESDBGAS
        if (RESDBGAS.length === 0) {
            return null
        }
        return (
            <div className='rtviewlayout'>
                <div style={{ display: "grid", gridTemplateColumns: '1fr 1fr', gridColumnGap: "5px" }}>
                    <GAS caseinfo={caseinfo} PickerDate={moment().valueOf()} searchbar={true} RESDBGAS={this.state.RESDBGAS}></GAS>
                    <RTbreathe caseinfo={caseinfo} PickerDate={moment().format("YYYY/MM/DD")} searchbar={true} QSPE={this.state.QSPE}></RTbreathe>
                </div>
            </div>
        );
    }
}

export default RTview
