import React, { Component } from 'react'
import Chronicordertable from './Chronic_ordertable'
import * as ApiGithub from '../../API/ApiGithub'

class Orderonchronic extends Component {
    state = {
        whiteListParser: []
    }
    componentDidMount() {
        ApiGithub.getGitHubJson("WhiteList.json")
        .then(data => {
            let parser = data.data;
            this.setState({
                whiteListParser: parser
            })
        })
        .catch(err => {
            console.log(err)
        })
    }
    render() {
        const { caseinfo, select_interval, UDORDER0,PickerDate } = this.props
        const { whiteListParser } = this.state
        const item_title = [{ title: "é«è¥å" }]
        return (
            <div style={{ marginTop: "15px" }}>
                <Chronicordertable title={item_title} caseinfo={caseinfo} chronic_state={select_interval} UDORDER0={UDORDER0} PickerDate={PickerDate} parser={whiteListParser}></Chronicordertable>
            </div>
        );
    }
}

export default Orderonchronic