import React, { Component } from 'react';
import RTtable from './RTtable'
import { DatePicker } from 'antd';
import moment from 'moment';

class GAS extends Component {
    state = {
        DateRange: [moment(), moment()]
    }
    render() {
        const { searchbar, caseinfo, source, PickerDate } = this.props
        const { RangePicker } = DatePicker;
        function formatRangeTimer(source, dateRange, PickerDate) {
            if (source === "crossection") {
                const et = moment(PickerDate).valueOf() + (31 * 60 * 60 * 1000)
                const st = et - (3 * 24 * 60 * 60 * 1000)
                return [st, et]
            }
            const st = dateRange[0].valueOf()
            const et = dateRange[1].valueOf()
            if (st === et) {
                return [st, st + (24 * 60 * 60 * 1000)]
            }
            return [st, et]

        }
        const fotmattimer = formatRangeTimer(source, this.state.DateRange, PickerDate)
        return (
            <div style={{ marginBottom: "20px" }}>
                <t1>GASç´¯ç©å ±å</t1>
                <div style={{ marginBottom: "5px", display: "grid", gridTemplateColumns: "220px 1fr 25px", gridColumnGap: "10px" }}>
                    {searchbar ? <RangePicker defaultValue={this.state.DateRange} onChange={(date, dateString) => { this.setState({ DateRange: [date[0].valueOf(), date[1].valueOf()] }) }} allowClear={false} /> : null}
                </div>
                <RTtable Timer={fotmattimer} caseinfo={caseinfo}></RTtable>
            </div>
        );
    }
}

export default GAS
