import React, { Component } from 'react';
import Treatmentordertable from './Treatmentordertable'
import { DatePicker, Select, Input, Button } from 'antd';
import searchlogo from '../../Image/svg/Search.svg'
import * as ApiGithub from '../../API/ApiGithub'
import moment from 'moment'

class Treatmentorderlayout extends Component {
    state = {
        class: "å¨é¨",
        parser: { "mappings": [] },
        startTime: '',
        endTime: '',
        selectedValue: 0,
        inputKey: '',
        filteredDataState: []
    }

    componentDidMount() {
        ApiGithub.getGitHubJson("Class.json")
            .then(data => {
                let parser = data.data.find(item => item.apply_condition.name === "Treatment");
                parser.mappings = [{ "key": "å¨é¨", "regex_str": null }, ...parser.mappings]
                console.log("getGitHubJson Treatment OK " + JSON.stringify(parser))
                this.setState({
                    parser: parser
                })
            })
            .catch(err => {
                console.log(err)
            })
    }

    onChangeClass = (value) => {
        this.setState({ class: value })
    }
    onChange(date, dateString) {
        console.log(date, dateString);
        const st = date[0].valueOf()
        const et = date[1].valueOf()
        this.setState({ startTime: st, endTime: et })
    }
    handleChange(value) {
        this.setState({ selectedValue: value })
    }
    onChangeinputKey(event) {
        this.setState({ inputKey: event.target.value })
    }
    onClickSearch() {
        const { TRTMNT01 } = this.props
        const parse = this.parse;
        let DataFormat = []

        let clusteredData = {}
        if (this.state.parser !== undefined) {
            clusteredData = parse(TRTMNT01, this.state.parser)
        }

        //filter data by selected class
        let filteredData = clusteredData[this.state.class];

        for (let index = 0; index < filteredData.length; index++) {
            const element = filteredData[index];
            const time = moment(element.ORBGNDT.toString().trim(), 'YYYY-MM-DD').valueOf()

            if (this.state.startTime <= time && time <= this.state.endTime && element.ORPROCED.includes(this.state.inputKey)) {
                DataFormat.push(element)
            }
        }
        this.setState({ filteredDataState: DataFormat })
    }

    parse = (inputs, parser) => {
        let mappings = parser.mappings;
        let allResult = {};
        mappings.forEach(mapping => {
            allResult[mapping.key] = []
        });
        allResult["å¨é¨"] = []

        inputs.forEach(input => {
            let content = input.ORPROCED;
            mappings.forEach(mapping => {
                const re = new RegExp(mapping.regex_str, mapping.regex_flags)
                const match = content.match(re)
                if (match !== null && match[0] !== "") {
                    allResult[mapping.key].push(input)
                }
            });

            allResult["å¨é¨"].push(input)
        });
        return allResult;
    }

    Search_bar() {
        const { searchbar } = this.props
        const { RangePicker } = DatePicker;
        const { Option } = Select;
        function onChange(date, dateString) {
            console.log(date, dateString);
        }

        const onChangeClass = this.onChangeClass;
        function handleChange(value) {
            console.log(`selected ${value}`);
        }
        if (searchbar) {
            return (
                <div style={{ display: "grid", gridTemplateColumns: "220px 130px 110px 150px auto", gridColumnGap: "10px", marginBottom: "5px" }}>
                    <RangePicker placeholder={["è«è¼¸å¥æ¥æ", "è«è¼¸å¥æ¥æ"]} onChange={this.onChange.bind(this)} allowClear={false} />
                    <Select defaultValue={this.state.class} style={{ width: 130 }} onChange={onChangeClass}>
                        {this.state.parser.mappings.map((mapping, index) => {
                            return <Option value={mapping.key}>{mapping.key}</Option>
                        })}
                    </Select>
                    <Select defaultValue={0} style={{ width: 110 }} onChange={this.handleChange.bind(this)}>
                        <Option value={0}>çæä¸­</Option>
                        <Option value={1}>å¤±æ/å·²çµæ/åæ¶</Option>
                    </Select>
                    <Input placeholder="è¼¸å¥è¥åééµå­" value={this.state.inputKey} onChange={this.onChangeinputKey.bind(this)} />
                    <Button shape="circle" icon="search" onClick={this.onClickSearch.bind(this)} />
                    {/* <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                        <img src={searchlogo} alt="searchlogo" />
                    </div> */}
                </div>
            );
        }
        else return null
    }

    render() {
        const { searchbar, TRTMNT01, caseinfo, PickerDate } = this.props
        const style = {
            crossection_style: { display: 'flex', justifyContent: 'space-between', alignItems: 'center', height: '50px', padding: "0px 5px", background: "#cccccc" },
            order_style: { display: 'flex', justifyContent: 'space-between', alignItems: 'center', height: '40px', paddingLeft: "5px", paddingRight: "5px" }
        }
        // const parse = this.parse;

        // let clusteredData = {}
        // if (this.state.parser !== undefined) {
        //     clusteredData = parse(TRTMNT01, this.state.parser)
        // }

        //filter data by selected class
        //let filteredData = clusteredData[this.state.class];
        return (
            <div>
                <div style={searchbar ? style.order_style : style.crossection_style}>
                    <t1>Treatment Order</t1>
                </div>
                {this.Search_bar()}
                <Treatmentordertable caseinfo={caseinfo} TRTMNT01={this.state.filteredDataState} PickerDate={PickerDate}></Treatmentordertable>
            </div>
        );
    }
}

export default Treatmentorderlayout
