import React, { Component } from 'react';
import * as d3 from "d3";
import moment from 'moment';

class Examchart extends Component {
    state = {
        change: false
    }
    componentDidUpdate() {

        const { svg_id } = this.props
        d3.select("#" + svg_id).remove()
        this.drawChart()
    }

    componentDidMount() {
        this.drawChart();
    }
    drawChart() {
        function formattimetoInteger(time) {
            return new Date(time).getTime()
        }
        const { Time, id_key, svg_id, max, min, lineChart } = this.props

        const sT = Time[0]
        const endT = Time[1]

        // éç
        const margin = { top: 20, right: 20, bottom: 30, left: 50 },
            width = 1000 - margin.left - margin.right,
            height = 500 - margin.top - margin.bottom;

        console.log(sT)
        console.log(endT)

        let x = d3.scaleTime().domain([sT, endT]).range([0, width])
        let y = d3.scaleLinear().domain([min - 1, max + 1]).range([height, 0])

        let svg = d3.select("#" + id_key)
            .append("svg")
            .attr("id", svg_id)
            .style("height", height + margin.top + margin.bottom)
            .style("width", width + margin.left + margin.right)
            .append("g")
            .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

        svg.append('g')
            .call(d3.axisBottom(x).ticks(5).tickFormat(d3.timeFormat('%m/%d')))
            .attr("transform", `translate(0,${height})`)
            .style('stroke', 'black')
            .style('stroke-width', 1);

        svg.append('g')
            .call(d3.axisLeft(y))
            .style('stroke', 'black')
            .style('stroke-width', 1);

        let line = d3.line()
            .x(d => x(moment(d.date, 'YYYYMMDDHHmm').toDate()))
            .y(d => y(d.value))
            .curve(d3.curveMonotoneX);

        let lines = svg.append('g').attr('class', 'lines');

        lines.selectAll("circle")
            .data(lineChart)
            .enter()
            .append('g')
            .append("circle")
            .attr("class", "lines-circle")
            .attr("cx", d => x(moment(d.date, 'YYYYMMDDHHmm').toDate()))
            .attr("cy", d => y(d.value))
            .attr('r', 5)
            .style('opacity', 1)
            .style('fill', "blue");

        lines
            .selectAll('.line-group')
            .data(lineChart)
            .enter()
            .append('g')
            .attr('class', 'line-group')
            .append('path')
            .attr('class', 'line')
            .attr('d', line(lineChart))
            .style('stroke', 'black')
            .style('fill', 'none');
    }

    render() {
        const { id_key } = this.props
        return (
            <div id={id_key}></div>
        );
    }
}

export default Examchart;
